
section{
  min-height:calc(100vh - 155px);
  margin-bottom:25px;
}
.MuiContainer-root{
  padding-top: 20px !important;
  min-height: 100vh;
}

.MuiContainer-root > h3{
  overflow:hidden;
}
button{
  border:1px solid black !important;
  margin:3px !important;
}
content{
  padding-top: 20px;
  padding-bottom: 20px;
  position: relative;
  display: table;
  margin: auto;
}

.MuiButton-label{
  display:inline !important;
}

h1,h2,h3,h4,h5{
  color:#2b2b2b;
  word-break:break-word;
}

a{
  color:black;
}
#root{
  background-color:#fbfbfb;
}
.MuiTabs-flexContainer > button:focus{
  outline:none !important;
}
a:hover{
  color:black;
}

footer{
  border-top:1px solid black;
  background:white;
  padding:45px;
  text-align: right;
  padding-top: 45px;
  padding-bottom: 30px;
}

.home-title{
  margin-bottom:25px;
}

.marketplace-nft-component{
  margin-top:50px;
  margin-bottom:50px;
}
.marketplace-nft-component-content{
  width:100%;
  margin:auto;
}

.MuiLinearProgress-colorPrimary {
  background-color:  grey !important;
}
.MuiCircularProgress-colorPrimary {
color:black !important;
}
.MuiLinearProgress-barColorPrimary,.MuiTabs-indicator {
  background-color: black !important;
}


@media only screen and (max-width: 450px) {
  .MuiContainer-root{
    padding-top: 5px !important;
    padding-left: 8px;
    padding-right: 8px;
  }
  span.MuiTab-wrapper{
    font-size:10px !important;
  }
  .MuiPaginationItem-root {
    margin: 0 0px !important;
  }
  content{
    display: block !important;
  }
}

.no-padding >div >div{
  padding:0 !important;
}

p{
  word-break: break-all;
}

